@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);

:root {
    --yellow: linear-gradient(180deg, #F8D49A -146.42%, #FAD79D -46.42%);
    --orange: #fca61f;
    --black: #242d49;
    --gray: #788097;
    --purple: linear-gradient(180deg, #BB67FF 0%, #C484F3 100%);
    --pink: #FF919D;
    --blue: #000dc7;
    --glass: rgba(255, 255, 255, 0.54);
    --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
    --activeItem: rgba(55, 153, 255, 0.33);
    --inputColor: rgba(40, 52, 62, 0.07);
    --cardColor: rgba(255, 255, 255, 0.64);
    --photo: #4CB256;
    --video: #4A4EB7;
    --location: #EF5757;
    --schedule: #E1AE4A;
}

.PostShare{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    background-color: white;
    padding: 1rem;
    border-radius: 1rem;
    flex-direction: row;
    justify-content: space-around;
}

.PostShare>img{
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
}

.PostShare>div{
    display: flex;
    flex-direction: column;
    width: 90%;
    grid-gap: 1rem;
    gap: 1rem;
}

/* Text input */
.PostShare>div>input{
    background-color: blue;
    border-radius: 10px;
    padding: 10px;
    font-size: 17px;
    border: none;
    outline: none;
}

.pfp {
    font-size: 16px;
}

.postOptions{
    display: flex;
    justify-content: space-around;
}

.option{

    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 12px;
}

.option:hover{
    cursor: pointer;
}

.ps-button{
    padding: 5px;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 12px;
    border-radius: 0.7rem;
    cursor: pointer;
    border-style: none;
    background-color: #347cff;
    color: white;
    text-shadow: 0px 0px 15px white;
    box-shadow: 0px 0px 10px 0px #96bbff;
}


.previewImage{
    position: relative;
}

.previewImage>svg{
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer;
}

.previewImage>img{
    width: 100%;
    max-height: 20rem;
    object-fit: cover;
    border-radius: 0.5rem;
}

@media screen and (max-width: 1200px) {
    .PostShare {
        width: 100%;
    }
    .PostShareWrapper {
        margin-top: 4rem;
        margin-bottom: 1rem;
    }
}


@media screen and (max-width: 768px) {
    .PostShare{
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 82vw;
        min-height: 15vh;
    }
    .PostShareWrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 2.5rem;
    }
    .postOptions {
        display: flex;
        justify-content: space-around;
        flex-direction:column;
    }
    .ps-button{
        cursor: pointer;
        margin: 1rem auto;
        font-size:14px;
        padding:10px;
        border-radius: 50px;
        width:45%;
    }
    .option{
        justify-content:space-around;
        width: 40%;
        margin: 2px auto;
        background-color:red;
        padding: 5px;
        padding-left: 5px;
        padding-right: 5px;
        border-radius: 50px;
        color: white !important;
        font-size: 14px;
    }
    .photooption{
        background-color: #4CB256;
        background-color: var(--photo);
    }
    .videooption{
        background-color: #4A4EB7;
        background-color: var(--video);
    }
    .locationoption{
        background-color: #EF5757;
        background-color: var(--location);
    }
    .scheduleoption{
        background-color: #E1AE4A;
        background-color: var(--schedule);
    }
}
:root {
    --body-bg: #f5f8ff;
    --box-bg: #fff;
    --input-bg: #f5f8ff;
    --txt-color: #2f2d2f;
    --txt-second-color: #ccc;
    --border-color: #4267b2;
    --box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}



.drop-file-input {
    position: relative;
    width: 400px;
    height: 200px;
    border: 2px dashed #4267b2;
    border: 2px dashed var(--border-color);
    border-radius: 20px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: #f5f8ff;

    background-color: var(--input-bg);
}

.drop-file-input input {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.drop-file-input:hover,
.drop-file-input.dragover {
    opacity: 0.6;
}

.drop-file-input__label {
    text-align: center;
    color: #ccc;
    color: var(--txt-second-color);
    font-weight: 600;
    padding: 10px;
}

.drop-file-input__label img {
    width: 100px;
}

.drop-file-preview {
    margin-top: 30px;
}

.drop-file-preview p {
    font-weight: 500;
}

.drop-file-preview__title {
    margin-bottom: 20px;
}

.drop-file-preview__item {
    position: relative;
    display: flex;
    margin-bottom: 10px;
    background-color: #f5f8ff;
    background-color: var(--input-bg);
    padding: 15px;
    border-radius: 20px;
}

.drop-file-preview__item img {
    width: 50px;
    margin-right: 20px;
}

.drop-file-preview__item__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.drop-file-preview__item__del {
    background-color: #fff;
    background-color: var(--box-bg);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    box-shadow: var(--box-shadow);
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.drop-file-preview__item:hover .drop-file-preview__item__del {
    opacity: 1;
}

@media screen and (max-width: 767px) {
    .drop-file-input {
        width: 50vw;
    }

}
:root {
    --body-bg: #f5f8ff;
    --box-bg: #fff;
    --input-bg: #f5f8ff;
    --txt-color: #2f2d2f;
    --txt-second-color: #ccc;
}

.WidgetWrapper {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.ButtonWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.DragndropWrapper {
    font-family: "Montserrat", sans-serif;
    background-color: #f5f8ff;
    background-color: var(--body-bg);
    color: #2f2d2f;
    color: var(--txt-color);
    font-weight: 400;
    line-height: 1.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.box {
    background-color: #fff;
    background-color: var(--box-bg);
    padding: 30px;
    border-radius: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}



@media screen and (max-width: 767px) {
    .DragndropWrapper {
        padding: 1.5rem;
    }
}

