@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap");

:root {
    --body-bg: #f5f8ff;
    --box-bg: #fff;
    --input-bg: #f5f8ff;
    --txt-color: #2f2d2f;
    --txt-second-color: #ccc;
}

.WidgetWrapper {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.ButtonWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.DragndropWrapper {
    font-family: "Montserrat", sans-serif;
    background-color: var(--body-bg);
    color: var(--txt-color);
    font-weight: 400;
    line-height: 1.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.box {
    background-color: var(--box-bg);
    padding: 30px;
    border-radius: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}



@media screen and (max-width: 767px) {
    .DragndropWrapper {
        padding: 1.5rem;
    }
}
